:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --Lime-green: hsl(163, 72%, 41%);
  --Bright-Red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --toggle-dark-mode-bg: linear-gradient(225deg,hsl(146, 68%, 55%) 0%, hsl(210, 78%, 56%) 98.02%);
  --toggle-dark-mode-text: hsl(0, 0%, 100%);
  --toggle-button-dark: var(--dark-bg);
  --light-bg:	hsl(0, 0, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --toggle-light-mode-bg:hsl(230, 22%, 74%);
  --toggle-light-mode-text: hsl(230, 19%, 60%);
  --toggle-button-light:hsl(228, 46%, 96%);
  --background: var(--dark-bg);
  --text-color:var(--dark-text2);
  --text-color-subheading:var(--dark-text1);
  --card-background:var(--dark-card);
  --card-hover-background:var(--dark-card-hover);
  --toggle-background:var(--toggle-dark-mode-bg);
  --toggle-text:var(--toggle-dark-mode-text);
  --toggle-button:var(--toggle-button-dark);
}

@media (prefers-color-scheme: light) {
  :root {
    --background:var(--light-bg);
    --text-color:var(--light-text2);
    --text-color-subheading:var(--light-text1);
    --card-background:var(--light-card);
    --card-hover-background:var(--light-card-hover);
    --toggle-background:var(--toggle-light-mode-bg);
    --toggle-text:var(--toggle-light-mode-text);
    --toggle-button:var(--toggle-button-light);
  }
}
body.dark {
  --background: var(--dark-bg);
  --text-color:var(--dark-text2);
  --text-color-subheading:var(--dark-text1);
  --card-background:var(--dark-card);
  --card-hover-background:var(--dark-card-hover);
  --toggle-background:var(--toggle-dark-mode-bg);
  --toggle-text:var(--toggle-dark-mode-text);
  --toggle-button:var(--toggle-button-dark);
}

body.light {
  --background:var(--light-bg);
  --text-color:var(--light-text2);
  --text-color-subheading:var(--light-text1);
  --card-background:var(--light-card);
  --card-hover-background:var(--light-card-hover);
  --toggle-background:var(--toggle-light-mode-bg);
  --toggle-text:var(--toggle-light-mode-text);
  --toggle-button:var(--toggle-button-light);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  --webkit-tap-highlight-color: transparent;
  transition: color 0.3s ease-out, background-color 0.3s ease-out;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 39.9375em) {
  .header__title {
    padding-bottom: 1.2rem;
    border-bottom: 1px solid #252a41;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color-subheading);
}
.toggle {
  border: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 39.9375em) {
  .toggle {
    margin: 18px 0 0 0;
    border: none;
    display: flex;
    justify-content: space-between;
  }
}
.toggle label {
  font-size: 0.875rem;
  color: var(--toggle-text);
  font-weight: 700;
}
.toggle label[for=dark] {
  margin-right: 0.5rem;
  cursor: pointer;
}
.toggle__wrapper {
  font-size: 12px;
  display: inline-block;
  vertical-align: middle;
  width: 48px;
  height: 1.5rem;
  border-radius: 1.5rem;
  /* = height */
  position: relative;
}
.toggle input[type=checkbox] {
  display: inline-block;
  margin: 0;
  width: 48px;
  height: 24px;
  opacity: 0;
  position: relative;
  z-index: 2;
  cursor: pointer;
}
.toggle__background {
  display: block;
  height: 100%;
  border-radius: 0.75rem;
  background: var(--toggle-background);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 0;
  transition: all 0.1s ease-out;
}
.toggle__button {
  background-color: var(--toggle-button);
  display: block;
  position: absolute;
  width: 1.125rem;
  height: 1.125rem;
  left: 4px;
  top: 3px;
  border-radius: 50%;
  transition: all 0.15s ease-out;
  z-index: 1;
}
.toggle #dark:checked ~ .toggle__button {
  left: 54%;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-background);
  color: var(--text-color-subheading);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 100ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover-background);
}
.card--facebook::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
  background: var(--facebook);
}
.card--twitter::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
  background: var(--twitter);
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.71%, var(--instagram-start) 100%);
}
.card--youtube::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
  width: 100%;
  background: var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -2px;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--text-color-subheading);
  text-transform: uppercase;
  letter-spacing: 5px;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--Lime-green);
}
.card__change--down {
  color: var(--Bright-Red);
}
.card__number {
  margin-left: 0.25rem;
}

.overview__heading {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.overview {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .overview {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .overview {
    grid-template-columns: repeat(4, 1fr);
  }
}
.overview__card {
  background: var(--card-background);
  color: var(--text-color-subheading);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  transition: background 100ms ease-in-out;
  cursor: pointer;
}
.overview__card:hover {
  background: var(--card-hover-background);
}
.overview__platform {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}
.overview__label {
  font-weight: 700;
  font-size: 14px;
}
.overview__numbers {
  display: flex;
  justify-content: space-between;
}
.overview__count {
  font-size: 32px;
  font-weight: bold;
  color: var(--text-color);
}
.overview__numbers {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.overview__percentage {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  font-size: 0.75rem;
  font-weight: 700;
  padding-bottom: 0.375rem;
}
.overview__percentage > img {
  margin-right: 4px;
}
.overview__percentage--up {
  color: var(--Lime-green);
}
.overview__percentage--down {
  color: var(--Bright-Red);
}